import React, { useEffect, useState } from 'react'
import Navbar from './Navbar'
import Footer from './Footer'
import { NavLink, useNavigate } from 'react-router-dom'



export default function UserAdmin() {
    const navigate = useNavigate();
    const [info, setInfo] = useState([]);

    const getInfo = async () => {
        try {
            const res = await fetch("/getinfo", {
                method: "GET",
                headers: {
                    "Content-Type": "application/json"
                },
                credentials: "include"
            });

            // Log the raw response
            console.log("Raw response:", res);

            // Check if the response is not OK and log the text response
            if (!res.ok) {
                const errorText = await res.text();
                console.log("Error response text:", errorText);

                if (res.status === 401) {
                    window.alert("You are not logged in. Redirecting to login page.");
                    navigate("/login");
                    return;
                }

                window.alert("res is not ok.");
                return;
            }

            const data = await res.json();

            if (res.status === 422) {
                console.log("Can't find data.");
                window.alert("Can't find data.");
            } else if (res.status === 200) {
                console.log("Data received");
                window.alert("Data received");
                console.log(data);
                setInfo(data);
                console.log(info);
            } else if (res.status === 400) {
                console.log("Error in catch");
                window.alert("Error in catch");
            } else {
                console.log("Unexpected error.");
                window.alert("Unexpected error.");

            }

        } catch (error) {
            console.log(error);
            // navigate("/login");
            window.alert("An error occurred. Please try again.");
        }
    };

    useEffect(() => { getInfo(); }, []);

    return (
        <div>
            <Navbar />
            <p className="text-center font-bold text-[25px] mt-5 mb-5">User Information</p>
            <div className="flex justify-center items-center p-6 border-2 w-[40%] m-auto mb-4 h-[64vh] border-gray-500 rounded-xl">

                <div className="flex gap-8" >
                    <div >
                        <p className="font-bold text-[18px] mb-2">Name</p>
                        <p className="font-bold text-[18px] mb-2">Email</p>
                        <p className="font-bold text-[18px] mb-2">Mobile No.</p>
                        <p className="font-bold text-[18px] mb-2">Change Password</p>
                    </div>
                    <div>
                        <p className="font-medium text-[18px] mb-2"> {info.name} </p>
                        <p className="font-medium text-[18px] mb-2"> {info.email} </p>
                        <p className="font-medium text-[18px] mb-2"> {info.number} </p>
                        <p className="font-medium text-[18px] mb-2"> <NavLink to="/forgot" className="bg-green-500 text-white font-semibold p-2 mt-3 rounded-lg">Change Password</NavLink> </p>
                    </div>

                </div>

            </div>
            <Footer />
        </div>
    )
}
