import React, { useState } from 'react';

const Calculator = () => {
    const [input, setInput] = useState('');
    const [result, setResult] = useState('');

    const handleClick = (value) => {
        setInput(input + value);
    };

    const handleClear = () => {
        setInput('');
        setResult('');
    };

    const handleCalculate = () => {
        try {
            const res = eval(input);
            setResult(res);
        } catch (error) {
            setResult('Error');
        }
    };

    return (
        <div className="p-4 bg-gray-200 rounded-lg shadow-md">
            <div className="mb-4 text-right">
                <div className="text-2xl">{input}</div>
                <div className="text-xl text-gray-600">{result}</div>
            </div>
            <div className="grid grid-cols-4 gap-2">
                {['7', '8', '9', '/'].map((value) => (
                    <button 
                        key={value} 
                        className="p-4 bg-white rounded shadow-md hover:bg-gray-100" 
                        onClick={() => handleClick(value)}
                    >
                        {value}
                    </button>
                ))}
                {['4', '5', '6', '*'].map((value) => (
                    <button 
                        key={value} 
                        className="p-4 bg-white rounded shadow-md hover:bg-gray-100" 
                        onClick={() => handleClick(value)}
                    >
                        {value}
                    </button>
                ))}
                {['1', '2', '3', '-'].map((value) => (
                    <button 
                        key={value} 
                        className="p-4 bg-white rounded shadow-md hover:bg-gray-100" 
                        onClick={() => handleClick(value)}
                    >
                        {value}
                    </button>
                ))}
                {['0', '.', '=', '+'].map((value) => (
                    <button 
                        key={value} 
                        className="p-4 bg-white rounded shadow-md hover:bg-gray-100" 
                        onClick={value === '=' ? handleCalculate : () => handleClick(value)}
                    >
                        {value}
                    </button>
                ))}
                <button 
                    className="col-span-2 p-4 bg-red-500 text-white rounded shadow-md hover:bg-red-600" 
                    onClick={handleClear}
                >
                    Clear
                </button>
            </div>
        </div>
    );
};

export default Calculator;
        