import React from 'react'
import { NavLink } from 'react-router-dom'
import Logo2 from "../img/Logo2.png";
import Account from "../img/user.png";
import Logout from "../img/logout.png";

function Navbar() {
    return (
        <div className="bg-yellow-400 p-3">
            <div className="flex justify-between items-center w-[1300px] m-auto">
                {/* <img src={Logo2} alt="logo" className="w-[150px]"/> */}
                <p className="text-[30px] text-white font-bold">TRACK<span className="text-green-600">KRO</span></p>
                <div className="flex gap-8">
                    <NavLink to="/admin"> <img src={Account} alt="logo" className="w-[30px]" /> </NavLink>
                    <NavLink to="/logout">  <img src={Logout} alt="logo" className="w-[30px]" /> </NavLink>
                </div>

            </div>
        </div>
    )
}

export default Navbar