import React, { useState } from 'react'
import Logo2 from "../img/Logo2.png";
import { NavLink, useNavigate } from 'react-router-dom';


function Login() {
    const navigate = useNavigate();
    const [user, setUser] = useState({ email: "", password: "" });
    const handleChange = (e) => {
        setUser({ ...user, [e.target.name]: e.target.value })
        console.log(user);
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        const { email, password } = user; 
        try {
            const res = await fetch("/login", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({
                    email,
                    password
                })
            });
            if (!res.ok) {
                console.log("Failed to Login.");
                window.alert("Failed to Login.");

            }
            const data = await res.json();
            if (res.status === 422) {
                console.log("User not found.");
                window.alert("User not found.");
            }
            else if (res.status === 500 || !data) {
                console.log("Error in Login, try again.");
                window.alert("Error in Login, try again.");
            } else if (res.status === 200) {
                console.log("User Succesfully Login.");
                window.alert("User Succesfully Login.");
                navigate("/home");
            }
            else if (res.status === 401) {
                console.log("Wrong password.");
                window.alert("Wrong password.");
            }
            else {
                console.log("Unexpected error.");
                window.alert("Unexpected error.");
            }


        } catch (error) {
            console.log(error);
            window.alert("An error occurred. Please try again.");
        }
    }

    return (
        <div className="h-[100vh] bg-yellow-400 flex justify-center items-center">

            <div className="bg-white p-6 rounded-xl w-[400px] m-4 shadow-gray-600 shadow-md">
                <img src={Logo2} alt="logo" className="w-[150px] m-auto" />
                
                <p className="font-semibold mt-1 text-center">Login with your account</p>
                <form className="flex flex-col justify-center items-center" onSubmit={handleSubmit}>
                    
                    <input type="email" placeholder="Enter Email Adress" name="email" value={user.email} onChange={handleChange} className="border-2  w-[100%] border-gray-200 p-2 m-2 rounded-md text-gray-500"/>
                    
                    <input type="password" placeholder="Enter your Password" name="password" value={user.password} onChange={handleChange} className="border-2 w-[100%] border-gray-200 p-2 m-2 rounded-md text-gray-500"/>
                    <button className="bg-green-500 w-[100%] text-white text-[18px] p-2 mt-2 rounded-lg">Login</button>
                </form>
                <div className="flex justify-between">
                <p className="mt-2   text-center text-[15px]"><span ><NavLink to="/forgot" className="text-green-500">Forgot Password</NavLink></span></p>
                <p className="mt-2 text-center text-[15px]"><span > <NavLink to="/register" className="text-green-500">Create new account</NavLink> </span></p>
                </div>
                <div></div>
                <div className="flex justify-around">
                <p className="mt-5 text-center text-[15px]"><span ><NavLink to="/contact" className="text-green-500"> Countact Us</NavLink></span></p>
                
                </div>
            </div>
        </div>
    )
}

export default Login