import React, { useState } from 'react';
import Logo2 from "../img/Logo2.png";


export default function ContactUs() {
  const [user, setUser] = useState({ name: "", email: "", message: "" });
  const handleChange = (e) => {
    setUser({ ...user, [e.target.name]: e.target.value })
    console.log(user);
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    const { name, email, message } = user; 
    try {
        const res = await fetch("/sendmessage", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                name,
                email,
                message
            })
        });
        if (!res.ok) {
            console.log("Failed to Login.");
            window.alert("Failed to Login.");

        }
        const data = await res.json();
        if (res.status === 422) {
            console.log("Empty feilds.");
            window.alert("Empty feilds.");
        }
        else if (res.status === 500 || !data) {
            console.log("Error in Login, try again.");
            window.alert("Error in Login, try again.");
        } else if (res.status === 200 || res.status === 201) {
            console.log("Message Sent, we will get back to you.");
            window.alert("Message Sent, we will get back to you.");
            // navigate("/home");
        }
        else if (res.status === 500) {
            console.log("error in sending message.");
            window.alert("error in sending message.");
        }
        else {
            console.log("Unexpected error.");
            window.alert("Unexpected error.");
        }


    } catch (error) {
        console.log(error);
        window.alert("An error occurred. Please try again.");
    }
}

  return (
    <div className="h-[100vh] bg-yellow-400 flex justify-center items-center">

      <div className="bg-white p-6 rounded-xl w-[400px] m-4 shadow-gray-600 shadow-md">
        <img src={Logo2} alt="logo" className="w-[150px] m-auto" />

        <p className="font-semibold mt-1 text-center">Contact Us</p>
        <form className="flex flex-col justify-center items-center" onSubmit={handleSubmit}>

          <input type="name" placeholder="Enter your Name" name="name" value={user.name} onChange={handleChange} className="border-2  w-[100%] border-gray-200 p-2 m-2 rounded-md text-gray-500" />
          <input type="email" placeholder="Enter Email Adress" name="email" value={user.email} onChange={handleChange} className="border-2  w-[100%] border-gray-200 p-2 m-2 rounded-md text-gray-500" />
          <textarea placeholder="Enter Message" name="message" value={user.message} onChange={handleChange} className="p-2 m-2 border-2  w-[100%] border-gray-200"></textarea>

          {/* <input type="password" placeholder="Enter your Password" name="password"  className="border-2 w-[100%] border-gray-200 p-2 m-2 rounded-md text-gray-500"/> */}
          <button className="bg-green-500 w-[100%] text-white text-[18px] p-2 mt-2 rounded-lg">Send Message</button>
        </form>

        <div></div>

      </div>
    </div>
  )
}
