import './App.css';
import GetStarted from './Component/GetStarted';
import Register from './Component/Register';
import Login from './Component/Login';
import ForgotPassword from './Component/ForgotPassword';
import Home from './Component/Home';
// import Contact from './Component/Contact';
import ResetPassword from './Component/ResetPassword';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import ContactUs from './Component/ContactUs';
import Logout from './Component/Logout';
import UserAdmin from './Component/UserAdmin';

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<GetStarted/>} />
          <Route path="/register" element={<Register/>} />
          <Route path="/login" element={<Login/>} />
          <Route path="/forgot" element={<ForgotPassword/>} />
          <Route path="/reset/:token" element={<ResetPassword/>} /> // sending the token recived throught reset mail, to check wheather its active or not.
          <Route path="/home" element={<Home/>} />
          <Route path="/contact" element={<ContactUs/>} />
          <Route path="/logout" element={<Logout/>} />
          <Route path="/admin" element={<UserAdmin/>} />
          {/* <Route path="/register" element={<Contact/>} /> */}
        </Routes>
      </BrowserRouter>
      {/* <GetStarted/> */}
    </div>
  );
}
export default App;
