import React, { useState } from 'react'
import Logo2 from "../img/Logo2.png";
import { useNavigate, useParams } from 'react-router-dom';


export default function ResetPassword() {
  const {token} = useParams(); // explore it
  const navigate = useNavigate();
  const [user, setUser] = useState({ password: ""});
  const handleChange = (e) => {
    setUser({ ...user, [e.target.name]: e.target.value })
    console.log(user);
}

const handleSubmit = async (e) => {
  e.preventDefault();
  const { password} = user; 
  try {
      const res = await fetch("/resetpassword/"+token, {
          method: "POST",
          headers: {
              "Content-Type": "application/json"
          },
          body: JSON.stringify({
              password
          })
      });
      if (!res.ok) {
          console.log("Failed to Send Email in Bckend.");
          window.alert("Failed to Send Email in Bckend.");

      }
      const data = await res.json();
      if (res.status === 200) {
          console.log("Password updated succesfully.");
          window.alert("Password updated succesfully.");
          navigate("/login")
      }
      else if (res.status === 500 || !data) {
          console.log("Error in sending the rest link, try again.");
          window.alert("Error in sending the rest link, try again.");
      }
      else {
          console.log("Unexpected error.");
          window.alert("Unexpected error.");
      }


  } catch (error) {
      console.log(error);
      window.alert("An error occurred. Please try again.");
  }
}

  return (
    <div className="h-[100vh] bg-yellow-400 flex justify-center items-center">

    <div className="bg-white p-6 rounded-xl w-[400px] m-4 shadow-gray-600 shadow-md">
        <img src={Logo2} alt="logo" className="w-[150px] m-auto" />
        <p className="font-semibold mt-1 text-center">Reset Password</p>
        <form className="flex flex-col justify-center items-center" onSubmit={handleSubmit}>
            
            <input type="password" placeholder="Enter New Password" name="password" value={user.password} onChange={handleChange} className="border-2  w-[100%] border-gray-200 p-2 m-2 rounded-md text-gray-500"/>
            
            
            <button className="bg-green-500 w-[100%] text-white text-[18px] p-2 mt-2 rounded-lg">Update Password</button>
        </form>
        <div className="flex justify-around">
        <p className="mt-4 text-center text-[15px]">Facing any issue?<br/><span className="text-green-500"> Countact Us</span></p>
        </div>
    </div>
</div>
  )
}
