import React from 'react'
import Logo2 from "../img/Logo2.png";
import "../Component/GetStarted.css"
import "../App.css"
import { NavLink } from 'react-router-dom';

function GetStarted() {
    return (
        <div className="h-[100vh] bg-yellow-400 flex  ">

            <div className="flex flex-col bg-white w-[60%] justify-center items-center">
                <img src={Logo2} className="w-[400px]" alt="logo" />
                <p className="font-bold text-[38px] text-green-950" > <span className="font-black text-[40px]">Track</span> your <span className="font-black text-[40px] text-green-700 "> Expenses </span>Easily</p>
            </div>

            <div className="  flex flex-col  justify-center w-[40%] items-end p-16">
                <p className="font-black text-[42px] text-black">Welcome to <span className="bg-white p-2 rounded-xl text-green-600">Track Kro</span> </p>
                <p className="text-[28px] font-normal  text-right text-white">Effortlessly manage your expenses and take control of your finances <br/>with Track Kro</p>
                <NavLink to="/login" className="bg-green-700 font-semibold text-[20px] hover:bg-green-900 text-white p-5 rounded-xl mt-5">Get Started</NavLink>
               
               
            </div>


        </div>
    )
}

export default GetStarted