import React, { useState } from 'react'
import Logo2 from "../img/Logo2.png";
import { NavLink, useNavigate } from 'react-router-dom';

function Register() {
    const navigate = useNavigate();
    const [user, setUser] = useState({ name: "", email: "", number: "", password: "" });
    const handleChange = (e) => {
        setUser({ ...user, [e.target.name]: e.target.value })
        console.log(user);
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        const { name, email, number, password } = user;
        try {
            const res = await fetch("/register", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({
                    name,
                    email,
                    number,
                    password
                })
            });
            if (!res.ok) {
                console.log("Failed to Registered.");
                window.alert("Failed to Registered.");

            }
            const data = await res.json();
            if (res.status === 422 || res.status === 400 || !data) {
                console.log("Error in registering, try again.");
                window.alert("Error in registering, try again.");
            } else if (res.status === 200) {
                console.log("User Succesfully Registered.");
                window.alert("User Succesfully Registered, Rediecting to Login Page.");
                navigate("/login");
            }
            else if (res.status === 500) {
                console.log("Email address already exists.");
                window.alert("Email address already exists.");
            }
            else {
                console.log("Unexpected error.");
                window.alert("Unexpected error.");
            }


        } catch (error) {
            console.log(error);
            window.alert("An error occurred. Please try again.");
        }
    }
    return (
        <div className="h-[100vh] bg-yellow-400 flex justify-center items-center">

            <div className="bg-white p-6 rounded-xl w-[400px] m-4 shadow-gray-600 shadow-md">
                <img src={Logo2} alt="logo" className="w-[150px] m-auto" />
                <p className="font-semibold mt-1 text-center">Creta your new account</p>

                <form className="flex flex-col justify-center items-center" onSubmit={handleSubmit}>
                    <input type="text" placeholder="Enter your Name" name="name" value={user.name} onChange={handleChange} className="border-2 active:border-yellow-600  w-[100%] border-gray-200  p-2 m-2 rounded-md text-gray-500" />
                    <input type="email" placeholder="Enter Email Adress" name="email" value={user.email} onChange={handleChange} className="border-2  w-[100%] border-gray-200 p-2 m-2 rounded-md text-gray-500" />
                    <input type="number" placeholder="Enter Mobile Number" name="number" value={user.number} onChange={handleChange} className="border-2  w-[100%] border-gray-200 p-2 m-2 rounded-md text-gray-500" />
                    <input type="password" placeholder="Enter your Password" name="password" value={user.password} onChange={handleChange} className="border-2 w-[100%] border-gray-200 p-2 m-2 rounded-md text-gray-500" />
                    <button className="bg-green-500 w-[100%] text-white text-[18px] p-2 mt-2 rounded-lg">Register</button>
                </form>
                <p className="mt-2 text-center text-[15px]">Already have account?<span ><NavLink to="/login" className="text-green-500 ml-1">Login</NavLink></span></p>
                <div className="flex justify-around">

                    <p className="mt-4 text-center text-[15px]"><span ><NavLink to="/contact" className="text-green-500"> Countact Us</NavLink></span></p>

                </div>
            </div>
        </div>
    )
}

export default Register